$(function () {
    //FAQ Accordion
    $('.faq-accordion').on('click', function () {
        $(this).next().slideToggle(200);
        $(this).toggleClass('open', 200);
    });

    // Mobile Filter Menu
    $('.display-menu').on('click', function () {
        var toolBarTranslate = document.getElementsByClassName('goog-te-banner-frame skiptranslate')[0];
        if (toolBarTranslate !== undefined) {
            $('#browse-offers .mobile-menu').addClass('with-translate-bar');
        } else {
            $('#browse-offers .mobile-menu').removeClass('with-translate-bar');
        }

        $("body, html").addClass("overflowHidden");
        $('.mobile-menu').addClass('show');
        $('.continue').show();
        $('#faq, .cards, .filter-container').hide();
    });
    $('.continue-btn, .filter-label .close').on('click', function () {
        $("body, html").removeClass("overflowHidden");
        $('.mobile-menu').removeClass('show');
        $('.continue').hide();
        $('#faq, .cards, .filter-container').show();
        hideFilters();
    });
    $('.reset-all').on('click', function () {
        scrollOffers();
        $("body, html").css("overflow-y", "visible");
    });
    // hide fileter container if empty
    function hideFilters() {
        if ($('.filter').length == 0) {
            $('.filter-container').hide();
        }
    }
    // Scroll to Browse Offers
    function scrollOffers() {
        $('html, body').animate({
            scrollTop: $("#browse-offers").offset().top
        }, 1);
    }
});