$(function () {
    $('.js-accordion-title').on('click', function () {
        var a_content = $(this).next();
        if (a_content.is(':visible')) {
            //alert('open');
            if (a_content.find('.checkbox input').is(':checked')) {
                a_content.find('input').closest('.checkbox').addClass('hide');
                a_content.find('input').filter(":checked").closest('.checkbox').removeClass('hide').addClass('show');
                if (!a_content.find('.more').length > 0) {
                    a_content.append('<p class="more">More</p>');
                } else {
                    a_content.find('input').closest('.checkbox').removeClass('hide');
                    a_content.find('.more').remove();
                }
                a_content.show();
            } else {
                a_content.slideToggle(100);
                $(this).toggleClass('open', 200);
                a_content.find('input').closest('.checkbox').removeClass('hide');
                a_content.find('.more').remove();
            }
        } else {
            //alert('close');
            var checked = a_content.find('input:checkbox:checked');
            var checks = a_content.find('input:checkbox');
            if(checked.length === 0) {
                checks.first().trigger( "click" );
            }
            a_content.slideToggle(200);
            $(this).toggleClass('open', 200);
        }
    });

    // Show More
    $(document).on('click', '.more',  function(){
        $(this).closest('.accordion-content').find('.checkbox').removeClass('hide');
        $(this).remove();
    });
});