// Fit slick slider in container
function slickPosition() {
    //$('.carousel').slick('setPosition');
    setTimeout(function () {
        if ($(".reveal.marketplace").is(":visible")) {
            $('.carousel').slick('setPosition');
        }
    }, 50);
}

function openModalScreens() {
    $('.screen-one .error').addClass('hide');
    $('.screen-one').show();
    $('.screen-two').hide();
    $('#marketpalce-modal').foundation('open');
    $('.reveal .carousel').slick('setPosition');
}

// Autoplay help modal
$('.help-modal').on('click', function () {
    $('#help-modal .image-carousel')[0].slick.refresh();
    $('#help-modal .image-carousel').slick('slickPlay');
});

// Fit slider inside the modal
$('.cards .card').on('click', function () {
    $('.marketplace .screen-two').hide();
    $('.screen-one .error').addClass('hide');
    slickPosition();
    populateData($(this));
});


// Close button
$('.close-button').on('click', function () {
    $('.screen-one').show();
    $('.screen-two').hide();
});


// Populte Modal Input Field
function populateData(card) {
    var emailValue = $('#memberEmail').text();
    $('.marketplace .screen-one input').val(emailValue);
    var url = card.attr('data-store-link');
    window.storeLink = url;
}

function populateEmail() {
    var emailValue = $('#memberEmail').text();
    $('.marketplace .screen-one input').val(emailValue);
}

